<template>
  <div>
    <div class="portfolioBox view">
      <div class="workInfo">
        <div class="ti">{{ work.title }}</div>
        <div class="sTi"></div>
        <div class="text" v-html="work.content">
        </div>
        <a :href="work.link" :title="work.title" target="_blank">
          <div class="btn"><font-awesome-icon :icon="['fas', 'link']" class="iconAw wh"/>前往網站</div>
        </a>
        <router-link to="/portfolioList">
          <div class="btn"><font-awesome-icon :icon="['fas', 'arrow-left']" class="iconAw wh"/>回列表</div>
        </router-link>
      </div>
      <div class="workPic">
        <img :src="getWorkPic(work.id)" :alt="work.title">
      </div>
    </div>
  </div>
</template>

<style>
.portfolioBox.view {
  max-width: 1600px;
  margin: 0 auto;
  margin: 150px auto 0;
  padding: 90px 0;
}
.workInfo {
  display: inline-block;
  width: 30%;
  padding: 0 50px;
  position: relative;
  vertical-align: top;
}
.workInfo .ti {
  font-size: 30px;
  display: block;
  width: 100%;
  font-weight: bold;
  color: #252525;
  letter-spacing: 10px;
  padding-bottom: 50px;
  position: relative;
  text-transform: uppercase;
}
.workInfo .sTi {
  font-size: 20px;
  line-height: 30px;
  color: #707070;
  letter-spacing: 5px;
}
.workInfo .sTi:after {
  content: '';
  background-color: #c9bb90;
  width: 50px;
  height: 3px;
  display: block;
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
  -webkit-transition-delay: .3s;
  transition-delay: .3s;
}
.workInfo .text {
  width: 100%;
  padding: 50px 0;
  line-height: 30px;
  /* background-color: #ddd9cd; */
  background-image: url(@/assets/images/irp.png);
}
.workInfo .btn {
  margin: 0 20px 0 0;
  padding: 0 20px;
  width: 35%;
  font-size: 15px;
  color: #fff;
  letter-spacing: 1px;
  /* text-shadow: 1px 1px 1px #ffffff; */
  border: 0;
  text-align: center;
  background-color: #a9a490;
  /* background-image: url(@/assets/images/irp_b.png); */
  padding: 10px;
  border-radius: 99px;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}
.workInfo .btn:hover {
  color: #707070;
  /* background-image: url(@/assets/images/irp.png); */
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}
.workPic {
  display: inline-block;
  width: 70%;
  padding: 0 50px;
}
@media only screen and (max-width: 1023px) {
  .workInfo {
    width: 100%;
    padding: 0 50px 50px;
  }
  .workPic {
    width: 100%;
  }
}
</style>

<script>
import { mapState, mapActions, mapMutations } from 'vuex';

export default {
  data() {
    return {
      id: this.$route.params.workId,
    }
  },
  computed: {
    ...mapState(['work']),
  },
  methods: {
    ...mapActions(['getWorks']),
    getWorkPic(item) {
      // return require(`@/assets/images/i_pro_${i + 1}.jpg`)
      return `./images/${item}/1.jpg`
    },
    
  },
  created() {
    this.getWorks(this.id); //把這頁面的 workId 帶入 mapActions 去執行 getWorks
  },
}
</script>